<template>
  Reels
		<h3>{{id}}</h3>
</template>

<script>
import axios, { Axios } from "axios";

export default {
	name: 'ProductDetailsReels',
	data(){
		return{
			reels:'',
			id:'',
			brand: '',
		}
	},
	created(){ 
		this.id = this.$route.params.id
		axios.get(`http://localhost:8083/product/reel?id=${this.id}`).then(response=> {
			this.reels = response.data
			console.log(response.data)
		})
	}
}
</script>
