<template>
  <main class="flex flex-row bgProducts loginbg m-auto mt-4">
      <img src="../assets/loginside.svg" alt="" class="flex -ml-1 -mt-0.5">
      <img src="../assets/rod.svg" alt="" class="absolute w-96">
      <div class="flex flex-col my-12">
        <h1 class="mainText bluecolor mainStroked text-3xl text-center">LOGIN</h1>
       <form action="" class="flex flex-col">
        <label for="email" class="secondaryText font-semibold text-lg mt-6">EMAIL</label>
        <input type="email" name="email" id="email" class="secondaryText py-2 px-8 text-start input"> 
        <img src="../assets/emaiil.svg" alt="" class="absolute w-12 mt-10 ml-52">
        <label for="password" class="secondaryText font-semibold text-lg mt-6">PASSWORD</label>
        <input :type="passwordFieldType" v-model="password" name="password" id="password"  @click="switchVisibility()" class="secondaryText py-2 px-8 text-start input">
         <button type="password" v-on:click="switchVisibility"><img src="../assets/eye.svg" class="absolute w-12 -mt-12 ml-52"></button>
        <button type="submit" class="mt-20 py-4 rounded-3xl text-2xl secondaryText font-bold buttonOrange transform duration-200 hover:scale-105">Login</button>
       </form>
      </div>
    

  </main>
</template>
<script>

export default {
 name: 'Login',
  data() {
    return {
      password: "",
      passwordFieldType: "password"
    }
  },
   methods: {
    switchVisibility() {
      this.passwordFieldType = this.passwordFieldType === "password" ? "text" : "password";
      event.preventDefault()
    }
  }
  
}
</script>
