<template>
  <main class="flex flex-row bgProducts loginbg m-auto mt-4">
      <img src="../assets/loginside.svg" alt="" class="flex -ml-1 -mt-0.5">
      <img src="../assets/rod.svg" alt="" class="absolute w-96">
      <div class="flex flex-col my-12">
        <h1 class="mainText bluecolor mainStroked text-3xl text-center">SIGN UP</h1>
       <form @submit.prevent="submitForm" method="post" class="flex flex-col">
        <label for="email" class="secondaryText font-semibold text-lg mt-6">EMAIL</label>
        <input v-model="email" type="email" name="email" id="email" class="secondaryText py-2 w-64 input text-left" > 
        <img src="../assets/emaiil.svg" alt="" class="absolute w-12 mt-10 ml-52">
        <label for="password" class="secondaryText font-semibold text-lg mt-6">PASSWORD</label>
        <input v-model="password" type="password" name="password" id="password" class="secondaryText py-2 text-start input">
        <img src="../assets/password.svg" alt="" class="absolute w-12 mt-36 ml-52">
        <label for="repeatPassword" class="secondaryText font-semibold text-lg mt-6">REPEAT PASSWORD</label>
        <input v-model="repeatPassword" type="password" name="repeatPassword" id="repeatPassword" class="secondaryText py-2 text-start input" >
        <button type="submit" class="mt-20 bg-yellow-200 py-4 rounded-3xl mainText font-bold buttonOrange transform duration-200 hover:scale-105">Submit</button>
       </form>
      </div>
    

  </main>
</template>
<script>
import axios from "axios"
import eventServices from "./services/EventServices"
export default {
 name: 'SignUp',
 headers:{
    "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Methods": "POST",
          "Access-Control-Allow-Headers": "Origin, Content-Type, X-Auth-Token"
  },
  data(){
    return{
        email: '',
        password: '',
        repeatPassword: ''
    }
  },
  methods:{
    submitForm(){
      axios.post('http://localhost:8083/user/create',{
       email: this.email,
       password: this.password,
       repeatPassword: this.repeatPassword
     })
     .then(response =>{
       console.log(response)
     })
    .catch(error => {
      console.log(error)
    })
    }
    
  }

}
</script>
