<template>
<main class="bgProducts flex flex-row my-4 lg:-mt-5">
	<ul class="mainText mainStroked flex flex-col justify-start items-center lg:px-10 pl-4 my-36 text-2xl">
      <li v-on:click="component='rods'" class="py-7 border-b-2 border-black hover:text-yellow-300 w-36 text-center cursor-pointer">RODS</li>
      <li v-on:click="component='reels'" class="py-7 border-b-2 border-black hover:text-yellow-300 w-36 text-center cursor-pointer">REELS</li>
      <li v-on:click="" class="py-7 border-b-2 border-black hover:text-yellow-300 w-36 text-center cursor-pointer">HANDLINES</li>
      <li v-on:click="" class="py-7 border-b-2 border-black hover:text-yellow-300 w-36 text-center cursor-pointer">FISHING HOOKS</li>
   </ul>
	 <div class="flex flex-row justify-center items-center">
		 <div v-for="rod in rods" class="flex flex-col justify-center">
		<img :src="rod.pictiure" >
		<h3>{{id}}</h3>
		<h1>{{rod.brand}}</h1>
		<h1>{{rod.price}}€</h1>
	</div>
	 </div>
	 	
</main>

 	

		
</template>

<script>
import axios, { Axios } from "axios";

export default {
	name: 'ProductDetails',
	data(){
		return{
			rods:'',
			id:'',
			brand: '',
		}
	},
	created(){ 
		this.id = this.$route.params.id
		axios.get(`http://localhost:8083/product/rod?id=${this.id}`).then(response=> {
			this.rods = response.data.rods
			console.log(response.data.rods)
		})
	}
}
</script>
